* {
  margin: 0;
  padding: 0;
}

#con {
  margin: 10px;
  padding: 10px;
}

#id {
  float: left;
  clear: both;
}

img {
  float: left;
}

ul {
  float: left;
}

li {
  list-style-type: none;
  font-size: 0.9em;
  color: darkgray;
}

ul li:first-child {
  font-size: 1.1em;
  color: darkblue;
}



